<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">{{pageTitle}}</el-row>
    <el-row type="flex" justify="start" style="margin-top: 30px;">
     <el-form ref="form" :model="form" :rules="rules" label-width="80px" label-position="right" style="min-width: 500px;">
        <el-form-item label="页面名称" prop="name">
          <el-row type="flex">
            <el-col :span="20">
              <el-input v-model="form.name" placeholder="请输入页面名称" type="textarea" autosize></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <div style="margin-left:10px; font-size: 14px; font-weight: 900;">页面内容<i class="avatar-limit">图片小于1M,格式jpg,png,jpeg</i></div>
        <template v-for="(item, index) in form.page_items">
              <el-form-item v-if="item.item_type == 'content'" label="文字" :key="index">
                <el-row type="flex">
                  <el-col :span="20">
                    <el-input v-model="item.content" placeholder="请输入文字 " type="textarea" autosize style="width: 100%;"></el-input>
                  </el-col>
                  <el-col :span="2" :offset="2">
                    <el-button icon="el-icon-delete" size="mini" @click="deleteTextImage(index)"></el-button>
                  </el-col>
                </el-row>
              </el-form-item>
              <el-form-item v-if="item.item_type == 'image'" label="图片" :key="index">
                <el-row type="flex">
                  <el-col :span="10">
                    <el-upload class="avatar-uploader"
                               action="http://szebra.cn/api/admin/images.json"
                               :show-file-list="false"
                               :drag="false"
                               :multiple="false"
                               name="image[file]"
                               :on-success="handleAvatarSuccess(item)"
                               :before-upload="beforeAvatarUpload">
                      <img v-if="item.image" :src="item.image" class="avatar">
                      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                  </el-col>
                  <el-col :span="2" :offset="2">
                    <el-button icon="el-icon-delete" size="small" @click="deleteTextImage(index)"></el-button>
                  </el-col>
                </el-row>
              </el-form-item>
        </template>
        <el-row style="margin-bottom: 30px;margin-top: 30px;">
          <el-button type="primary" plain size="small" @click="addText">添加文字</el-button>
          <el-button type="primary" plain size="small" @click="addImage">添加图片</el-button>
        </el-row>
        <el-button type="primary" @click="submitForm('form')">保存</el-button>
      </el-form>
    </el-row>
  </div>
</template>
<script>

export default {
  components: {
  },

  data () {
    return {
      pageState: '', // 1新增，2编辑，3预览
      pageTitle: '', // 页面标题
      pageId: '', // 自定义页面id
      form: {
        name: '',
        page_items: []
      },
      rules: { // 校验规则
        name: { required: true, message: '请输入资讯标题名称', trigger: 'blur' }
      }
    }
  },

  watch: {
    pageState (newValue) {
      let name = '自定义'
      switch (newValue) {
        case 1:
          this.pageTitle = `新增${name}`
          break
        case 2:
          this.pageTitle = `编辑${name}`
          break
        case 3:
          this.pageTitle = `${name}详情`
          break
      }
    }
  },

  methods: {
    // 查询自定页面详情
    queryPageDetail () {
      let params = {
        page_id: this.pageId
      }
      this.$http.pageDetail(params, 'get').then(res => {
        let result = res.data
        let resultRows = result.result_rows || {}
        if (result === 0) {
          this.form = resultRows
        } else {
          this.$message.error(`${result.retcode}${result.message}`)
        }
      })
    },
    // 添加文字
    addText () {
      this.form.page_items.push({
        item_type: 'content',
        image: '',
        content: ''
      })
    },

    // 添加图片
    addImage () {
      this.form.page_items.push({
        item_type: 'image',
        image: '',
        content: ''
      })
    },

    // 删除图片或者文字
    deleteTextImage (index) {
      this.form.page_items.splice(index, 1)
    },

    // 提交表单
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let params = {}
          let parasmWrap = {}

          params.name = this.form.name
          params.page_items_attributes = this.form.page_items.map((item, index) => {
            return Object.assign({ position: index }, item)
          })
          parasmWrap.page = params
          if (this.pageState === 1) {
            this.$http.addPage(parasmWrap, 'post').then(res => {
              let result = res.data

              if (result.retcode === 0) {
                this.$message.success('新增成功')
                setTimeout(() => {
                  this.$router.push({
                    path: '/page/list'
                  })
                }, 300)
              } else {
                this.$message.error('新增失败')
              }
            })
          } else {
            this.$http.editPage(parasmWrap, 'patch').then(res => {
              let result = res.data
              if (result.retcode === 0) {
                this.$message.success('修改成功')
                setTimeout(() => {
                  this.$router.push({
                    path: '/page/list'
                  })
                }, 300)
              } else {
                this.$message.error('修改成功')
              }
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    handleAvatarSuccess (data) {
      return function (res) {
        // res为接口返回数据
        let resultRows = res.result_rows || {}
        let retcode = res.retcode
        if (retcode === 0) {
          this.$message.success('上传成功')
          data.image = resultRows.url
          data.image_attributes = {}
          data.image_attributes.id = resultRows.id
        } else {
          this.$message.error(`${retcode}${res.message}`)
        }
      }
      // this.imageUrl = URL.createObjectURL(file.raw);
    },

    beforeAvatarUpload (file) {
      const picTypeRigth = file.type === 'image/jpeg' && file.type === 'image/jpg' && file.type === 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!picTypeRigth) {
        this.$message.error('上传图片只能是 jpg、jpeg、png 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!')
      }
      return picTypeRigth && isLt2M
    }
  },

  mounted () {
    this.pageState = +this.$route.query.pageState || 1
    this.pageId = this.$route.query['page_id'] || ''
    if (this.pageState !== 1) {
      this.queryPageDetail()
    }
  }
}
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .avatar-limit{
    font-size: 12px;
    font-weight: 300;
    margin-left: 20px;
    color: #909399;
  }
</style>
